<template>
    <div id="join">
        <p>회원가입</p>
        <label for="id">ID: </label>
        <input type="text" v-model="join.id"><br>
        <label for="">Name: </label>
        <input type="text" v-model="join.name"><br>
        <label for="pw">PW: </label>
        <input type="text" v-model="join.pw"><br>
        <button @click="joinUser">회원가입</button>
    </div>
</template>
<script>
export default {
    props: [
        'id',
        'name',
        'pw'
    ],
    components: {},
    data() {
        return {
            join: {}
        }
    },
    setup() {},
    created() {
        this.join.id = this.id
        this.join.name = this.name
        this.join.pw = this.pw
    },
    mounted() {},
    unmounted() {},
    methods: {
        joinUser() {
            // console.log("joinUser")
            this.$emit('child', this.join)
        }
    }
}
</script>
<style scoped>
#join {
  width: 50%;
  background: rgb(205, 241, 148);
  margin: 0 auto;
  padding: 2rem 0;
}

label {
    padding: 0 2rem;
}

button {
    margin-top: 2rem;
    background: white;
    border: 1px solid #ccc;
    padding: 0.3rem;
}

p {
    font-size: 20px;
    font-weight: bold;
}
</style>