<template>
    <div id="wrap">
        <JoinEdit
        :id="id"
        :name="name"
        :pw="pw"
        @child='parents'></JoinEdit>
    </div>
</template>
<script>
import JoinEdit from "./JoinEdit.vue"

export default {
    components: {
        JoinEdit
    },
    data() {
        return {
            exampleData: '',
        }
    },
    setup() {},
    created() {},
    mounted() {},
    unmounted() {},
    methods: {
        parents(join) {
            // console.log('부모가 받았어요')
            this.id = join.id
            this.name = join.name
            this.pw = join.pw
            alert("ID: " + this.id + ", Name: " + this.name)
        }
    }
}
</script>